export default {
  async asyncData({ $content, params, error }) {
    let post
    try {
      post = await $content('publications', params.slug).fetch()
      // OR const article = await $content(`articles/${params.slug}`).fetch()
    } catch (e) {
      error({ message: 'Publication not found' })
    }

    return {
      post,
    }
  },
}
